"use client";

import { useLocale, useTranslations } from "next-intl";
import { useQueryState } from "nuqs";
import { Checkbox, Link as ChakraLink } from "@chakra-ui/react";
import { Link } from "@/i18n/routing";

export const Legal = () => {
  const t = useTranslations();
  const language = useLocale();

  const [accept, setAccept] = useQueryState("terms_accepted");

  const handleChange = (value) => {
    const checked =
      typeof value === "object" && "checked" in value ? value.checked : value;
    setAccept(checked);
  };

  return (
    <Checkbox.Root
      size="sm"
      colorPalette="pink"
      defaultChecked={accept == "true" ? true : false}
      onCheckedChange={handleChange}
    >
      <Checkbox.HiddenInput />
      <Checkbox.Control />
      <Checkbox.Label
        fontSize={"15px"}
        lineHeight={"24px"}
        fontWeight={"400"}
        pl={"8px"}
      >
        {t("legal_agree")}{" "}
        <ChakraLink
          as={Link}
          color={"#004143"}
          href={`/legal/terms-of-service`}
        >
          {t("terms_of_service")}
        </ChakraLink>{" "}
        {t("and")}
        <ChakraLink as={Link} color={"#004143"} href={`/legal/privacy-policy`}>
          {t("privacy_policy")}
        </ChakraLink>{" "}
        {t("legal_understand")}
      </Checkbox.Label>
    </Checkbox.Root>
  );
};
